/* 讓整體頁面充滿螢幕 */
.container {
  display: flex;
  height: 100vh;  /* 設定高度為視窗高度 */
}

/* 左側區塊 */
.left-panel {
  width: 200px;  /* 固定左側區域寬度為 200px */
  background-color: #f5f5f5;  /* 左側區的背景顏色，比右側白一點 */
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  padding-right: 10px;
  height: 100vh;  /* 高度為視窗高度 */
  position: relative;  /* 讓底層區域能夠定位 */
  overflow: hidden;  /* 防止右側滾動影響左側 */
}

/* 固定在左側上方的資訊欄 */
.info-bar {
  position: sticky;
  top: 0;
  background-color: #fefefe;  /* 比左側背景顏色白一點 */
  padding-top: 10px;  /* 上方間距 */
  padding-bottom: 10px;  /* 下方間距 */
  display: flex;
  align-items: center;
  gap: 10px;
  border-bottom: 2px solid #ddd;
  z-index: 1;
  width: 100%;
}

/* 個人圖片 */
.profile-img {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  object-fit: cover;
}

/* 帳戶名稱 */
.username {
  font-size: 18px;
  font-weight: bold;
}

/* 顯示頁簽的區域 */
.tabs {
  margin-top: 20px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  flex-grow: 1; /* 填滿剩餘空間 */
  overflow-y: auto;  /* 讓左側區域可以垂直滾動 */
}

/* 頁簽項目 */
.tab {
  padding: 10px;
  cursor: pointer;
  border: 1px solid #ccc;
  margin-bottom: 5px;
  border-radius: 4px;
  background-color: #fff;
  transition: background-color 0.2s;
}

/* 選中的頁簽 */
.tab.active {
  background-color: #e3e3e3;
}

/* 左側底部固定區域 */
.fixed-bottom {
  position: sticky;
  bottom: 0;  /* 固定在容器底部 */
  background-color: #f9f9f9;
  overflow-y: auto;  /* 讓左側區域可以垂直滾動 */
  padding: 10px;
  border-top: 1px solid #ccc;
  z-index: 1;
  width: 100%;
}

/* 右側區塊 */
.right-panel {
  flex-grow: 1;  /* 右側區域占據剩餘空間 */
  background-color: #fff;  /* 背景顏色 */
  overflow-y: auto;  /* 讓右側區域可以垂直滾動 */
  height: 100vh;  /* 高度為視窗高度 */
  box-sizing: border-box;  /* 確保 padding 計算進寬度 */
}

.add-tab-btn {
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
  width: 100%;
  text-align: center;
  transition: background-color 0.3s;
}

.add-tab-btn:hover {
  background-color: #45a049;
}

/* 顯示橫幅圖片的區域 */
.banner {
  width: 100%;
  height: 150px;  /* 設定橫幅區塊高度 */
  margin-bottom: 20px;  /* 與標題之間的間距 */
  overflow: hidden;
}

.banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;  /* 讓圖片按比例顯示，並裁剪過多部分 */
}

/* 編輯標題的文字框 */
.tab-title-input {
  width: 100%;
  font-size: 36px;  /* 增大字體大小 */
  padding: 20px;
  border: none;  /* 去除邊框 */
  background-color: transparent;  /* 背景透明 */
  outline: none;  /* 去除焦點邊框 */
  box-sizing: border-box;
  font-weight: bold;  /* 使標題更顯眼 */
  color: #333;  /* 使用深色文字 */
}

/* 當使用者點擊編輯框時，添加簡單的背景色 */
.tab-title-input:focus {
  background-color: #f5f5f5;  /* 焦點狀態時改變背景顏色 */
}

/* 控制新增區塊按鈕 */
.add-block-btn {
  color: rgb(159, 159, 159);
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 200px;
  margin-top: 10px;
  margin-left: 20px;
  transition: background-color 0.3s;
}

.add-block-btn:hover {
  background-color: rgb(159, 159, 159);
  color: white;
}

/* 控制區塊選擇彈出選單 */
.block-menu {
  margin-top: 10px;
  width: 200px;
  background-color: white;
  border: 1px solid #ddd;
  padding: 10px;
  margin-left: 20px;
  border-radius: 4px;
}

.block-menu button {
  display: block;
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
}

.block-menu button:hover {
  background-color: #ddd;
}

/* 文字區塊 */
.block {
  margin-bottom: 20px;  /* 每個區塊底部間距 */
}

/* 文字區塊 */
.block {
  margin-bottom: 20px;  /* 每個區塊底部間距 */
}

.text-block {
  width: 95%;  /* 確保文字框寬度不超過父容器 */
  max-width: 100%;  /* 設定最大寬度為父容器的寬度 */
  min-height: 50px;  /* 設定最小高度 */
  position: relative;
  padding: 10px;
  font-size: 16px;
  margin-left: 20px;
  border-radius: 4px;
  border: none;  /* 初始狀態下不顯示邊框 */
  background-color: transparent;  /* 背景透明 */
  resize: none;  /* 禁止手動調整大小 */
  box-sizing: border-box;  /* 確保 padding 計算在寬度內 */
  outline: none;  /* 去除預設焦點樣式 */
  background-color: transparent;
}

.text-block:focus, .sheet-block:focus {
  border: 1px solid #ddd;  /* 顯示邊框 */
}


/* 表格區塊樣式 */
.sheet-table {
  width: 95%;
  border-collapse: collapse;  /* 合併邊框 */
  margin-top: 10px;
  margin-left: 20px;
}

.sheet-table th,
.sheet-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

.sheet-table th {
  background-color: #f4f4f4;  /* 表頭的背景色 */
}

.sheet-table tr:nth-child(even) {
  background-color: #f9f9f9;  /* 偶數行的背景色 */
}

.sheet-table tr:hover {
  background-color: #f1f1f1;  /* 滑鼠懸停時的背景色 */
}

.column-title-input{
  border: none;  /* 初始狀態下不顯示邊框 */
  background-color: transparent;  /* 背景透明 */
}

.column-title-input:focus{
  border: 1px solid #ddd;  /* 顯示邊框 */
}

/*刪除按鈕*/
.delete-icon {
  color: rgb(255, 109, 109);
  cursor: pointer;
  margin-left: 8px;
  font-size: 18px;
  /* display: none; 預設隱藏 */
}

.delete-icon:hover {
  opacity: 0.7;
}

